"use client";
// React and Next
import { useEffect, useState } from "react";

// API
import { CheckoutCartListApi } from "@/api/serversideaction/cartapi";

// 3rd Party
import { useRouter } from "nextjs-toploader/app";
// Redux Imports
import { useDispatch } from "react-redux";
import {
  checkoutItems,
  checkoutPriceWithoutTax,
  checkoutPriceWithTax,
  enableBackOrder,
  enableFailedOrder,
  selectedBillingAddress,
  selectedCartItems,
  selectedShippingAddress,
} from "@/redux/slices/checkout";

// Components
import CheckoutPriceDetails from "../CheckoutPriceDetails";
import EmptyCart from "./EmptyCart";
import CheckoutProduct from "./CartProduct";
import { useTranslation } from "react-i18next";

export default function Cart({ token, addonlist, cartPath }) {
  const router = useRouter();
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const [trigger, setTrigger] = useState(0);
  const [cartProducts, setCartProducts] = useState([]);
  const [loader, setLoader] = useState(false);
  const [productPricingGroupList, setProductPricingGroupList] = useState([]);

  useEffect(() => {
    const cartdatas = JSON.parse(localStorage.getItem(cartPath));
    if (cartdatas) {
      setCartProducts(cartdatas);
    } else {
      setCartProducts([]);
    }
  }, [trigger]);

  useEffect(() => {
    if (token && cartPath == "cartItems") {
      CheckoutCartListApi(
        dispatch,
        setCartProducts,
        setTrigger,
        trigger,
        addonlist,
        setProductPricingGroupList
      );
    }
  }, [token]);

  useEffect(() => {
    let totalPrice = 0;
    let totalTaxPrice = 0;
    const cartdatas = JSON.parse(localStorage.getItem(cartPath));
    cartdatas?.forEach((item) => {
      // Calculation for Price without tax
      let price =
        item?.priceGroup == 0
          ? Number(item?.pricerefer ? item?.pricerefer : item?.price)
          : Number(item?.price);
      totalPrice += item?.productCount * price;

      // Calculation for Tax price
      if (item?.taxType == 2) {
        let count = Number(item?.price) * (Number(item?.taxValue) / 100);
        if (item?.productCount) {
          totalTaxPrice += count * item?.productCount;
        } else {
          totalTaxPrice += count;
        }
      } else {
        if (item?.productCount) {
          totalTaxPrice += Number(item?.taxValue) * item?.productCount;
        } else {
          totalTaxPrice += Number(item?.taxValue);
        }
      }
    });
    dispatch(checkoutPriceWithoutTax(totalPrice));
    dispatch(checkoutPriceWithTax(totalTaxPrice));
  }, [trigger]);

  const handlePayNow = () => {
    setLoader(true);
    dispatch(selectedShippingAddress(null));
    dispatch(selectedBillingAddress(null));
    if (cartPath == "cartItems") {
      // if (PaynowValidation()) {
      localStorage.setItem(cartPath, JSON.stringify(cartProducts));
      router.push("/choose-address");
      dispatch(checkoutItems(cartPath));
      dispatch(selectedCartItems(cartProducts));
      dispatch(enableBackOrder(false));
      dispatch(enableFailedOrder(false));
      // }
    } else {
      if (cartProducts?.length > 0) {
        localStorage.setItem(cartPath, JSON.stringify(cartProducts));
        router.push("/choose-address");
        dispatch(checkoutItems(cartPath));
        dispatch(selectedCartItems(cartProducts));
        dispatch(enableBackOrder(true));
        dispatch(enableFailedOrder(false));
      }
    }
    setTimeout(() => {
      setLoader(false);
    }, 2000);
  };

  return (
    <div className="max-w-screen-xl px-4 py-4 m-auto mb-20 sm:px-4">
      {cartProducts?.length > 0 ? (
        // Cart content
        <>
          <div className="p-[20px]">
            <h3 className="font-semibold text-[#202124] text-xl leading-6">
              {cartProducts?.length}{" "}
              {cartProducts?.length > 1
                ? `${t("checkout.basket")}`
                : `${t("checkout.basket1")}`}
            </h3>
          </div>

          <div className="flex lg:flex-row flex-col items-start lg:rtl:space-x-[unset] lg:space-x-5 xl:space-x-10">
            <div className="flex flex-col w-full space-y-4 lg:rtl:ml-10">
              {cartProducts &&
                cartProducts?.map((product, i) => (
                  // Cart Product
                  <CheckoutProduct
                    key={i}
                    product={product}
                    token={token}
                    addonlist={addonlist}
                    cartPath={cartPath}
                    cartProducts={cartProducts}
                    productPricingGroupList={productPricingGroupList}
                    setTrigger={setTrigger}
                  />
                ))}
            </div>

            <CheckoutPriceDetails
              token={token}
              handlePayNow={handlePayNow}
              cartPath={cartPath}
              addonlist={addonlist}
              loader={loader}
              valid={true}
            />
          </div>
        </>
      ) : (
        // Empty cart
        <EmptyCart />
      )}
    </div>
  );
}
