// API
import { removeFromCartApi } from "@/api/serversideaction/cartapi";

// 3rd Party
// Redux
import {
  checkoutCouponData,
  couponApplied,
  couponCode,
  couponDiscountValue,
  enteredCouponCode,
} from "@/redux/slices/checkout";
import { viewCartCount } from "@/redux/slices/userSlice";
import { useDispatch } from "react-redux";

// Components
import { useTranslation } from "react-i18next";

const CheckoutRemoveProduct = ({
  product,
  token,
  cartPath,
  cartProducts,
  setTrigger,
}) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();

  const handleRemoveProduct = () => {
    if (cartPath == "cartItems") {
      if (token) {
        removeFromCartApi(
          product?.productId,
          product?.price,
          product?.skuName,
          cartProducts,
          setTrigger,
          dispatch,
          product?.skuId
        );
      } else {
        const cartlist = cartProducts.filter(
          (item) => item?.skuId !== product?.skuId
        );
        localStorage.setItem("cartItems", JSON.stringify(cartlist));
        dispatch(viewCartCount(cartlist));
        setTrigger((pre) => pre + 1);
      }
    } else {
      const cartlist = cartProducts.filter(
        (item) => item?.skuId !== product?.skuId
      );
      localStorage.setItem(cartPath, JSON.stringify(cartlist));
      setTrigger((pre) => pre + 1);
    }
    dispatch(checkoutCouponData(""));
    dispatch(couponDiscountValue(0));
    dispatch(couponCode(""));
    dispatch(enteredCouponCode(""));
    dispatch(couponApplied(false));
  };

  return (
    <div className="flex items-center space-x-[10px]">
      <a
        role="button"
        onClick={handleRemoveProduct}
        className="relative flex items-center space-x-2 rtl:space-x-[unset] py-[10px] rounded w-max font-normal text-[#202124] text-sm hover:underline transition-all duration-300 cursor-pointer"
      >
        <img src="/img/delete.svg" alt="delete" className="rtl:ml-2" />{" "}
        <p className="font-normal text-[#202124] text-sm leading-4">
          {t("checkout.remove")}
        </p>
      </a>
    </div>
  );
};

export default CheckoutRemoveProduct;
