// API
import { CartIncreaseAndDecreaseApi } from "@/api/serversideaction/cartapi";

// 3rd Party
// Redux
import {
  checkoutCouponData,
  couponApplied,
  couponCode,
  couponDiscountValue,
  enteredCouponCode,
} from "@/redux/slices/checkout";
import { viewCartCount } from "@/redux/slices/userSlice";
import { useDispatch } from "react-redux";

// Components
import ToastMessage from "@/Components/ToastMessage";
import { useTranslation } from "react-i18next";

const CheckoutProductQuantityControl = ({
  product,
  token,
  addonlist,
  cartPath,
  productPricingGroupList,
  setTrigger,
}) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();

  const [count, max, min, id, skuId] = [
    product?.productCount,
    product?.maxQuantityAllowedCart,
    product?.minQuantityAllowedCart,
    product?.productId,
    product?.skuId,
  ];

  const handleIncrease = () => {
    const cartdatas = JSON.parse(localStorage.getItem(cartPath));
    const updatedCartData = cartdatas.map((product) => {
      if (product?.productId === id && skuId === product?.skuId) {
        if (
          addonlist?.["product-price-group"] &&
          addonlist?.["product-price-group"]?.status === 1
        ) {
          if (token) {
            const priceGroup = productPricingGroupList?.filter(
              (product) => product?.skuId === product?.skuId
            );
            const highQuantity = priceGroup?.at(-1)?.maxQuantity;
            if (product?.hasStock == 1) {
              if (product.productCount < max) {
                if (priceGroup?.length > 0) {
                  const newProductCount = product.productCount + 1;
                  const tirePrice = priceGroup?.filter(
                    (price) => price?.maxQuantity <= newProductCount
                  );
                  const newPrice =
                    tirePrice?.length > 0
                      ? tirePrice.at(-1)?.price
                      : product.price;

                  const priceGroups = tirePrice?.length > 0 ? 1 : 0;
                  if (cartPath == "cartItems") {
                    CartIncreaseAndDecreaseApi({
                      ...product,
                      productCount: newProductCount,
                      price: newPrice,
                      priceGroup: priceGroups,
                    });
                  }
                  return {
                    ...product,
                    productCount: newProductCount,
                    price: newPrice,
                    priceGroup: priceGroups,
                  };
                } else {
                  return handleIncreaseByDefault(product, max);
                }
              } else {
                ToastMessage({
                  type: "error",
                  message: t("toastMessage.maximumLimitCart"),
                });
              }
            } else {
              if (priceGroup?.length > 0) {
                const newProductCount = product.productCount + 1;
                const tirePrice = priceGroup?.filter(
                  (price) => price?.maxQuantity <= newProductCount
                );
                const newPrice =
                  tirePrice?.length > 0
                    ? tirePrice.at(-1)?.price
                    : product.price;

                const priceGroups = tirePrice?.length > 0 ? 1 : 0;
                if (cartPath == "cartItems") {
                  CartIncreaseAndDecreaseApi({
                    ...product,
                    productCount: newProductCount,
                    price: newPrice,
                    priceGroup: priceGroups,
                  });
                }
                return {
                  ...product,
                  productCount: newProductCount,
                  price: newPrice,
                  priceGroup: priceGroups,
                };
              } else {
                return handleIncreaseByDefault(product, max);
              }
            }
          } else {
            return handleIncreaseByDefault(product, max);
          }
        } else {
          return handleIncreaseByDefault(product, max);
        }
      }
      return product;
    });

    localStorage.setItem(cartPath, JSON.stringify(updatedCartData));
    setTrigger((pre) => pre + 1);
    if (cartPath === "cartItems") {
      dispatch(viewCartCount(updatedCartData));
    }
    dispatch(checkoutCouponData(""));
    dispatch(couponDiscountValue(0));
    dispatch(couponCode(""));
    dispatch(enteredCouponCode(""));
    dispatch(couponApplied(false));
  };

  const handleIncreaseByDefault = (product, max) => {
    if (product?.hasStock == 1) {
      if (product.minQuantityAllowedCart && product.productCount < max) {
        const newProductCount = product.productCount + 1;
        if (token && cartPath == "cartItems") {
          CartIncreaseAndDecreaseApi({
            ...product,
            productCount: newProductCount,
          });
        }
        return {
          ...product,
          productCount: newProductCount,
          price: product.price,
          priceGroup: 0,
        };
      } else {
        ToastMessage({
          type: "error",
          message: t("toastMessage.maximumLimitCart"),
        });
        return product;
      }
    } else {
      const newProductCount = product.productCount + 1;
      if (token && cartPath == "cartItems") {
        CartIncreaseAndDecreaseApi({
          ...product,
          productCount: newProductCount,
        });
      }
      return {
        ...product,
        productCount: newProductCount,
        price: product.price,
        priceGroup: 0,
      };
    }
  };

  const handleDecrease = () => {
    let cartdatas = JSON.parse(localStorage.getItem(cartPath));
    cartdatas = cartdatas.map((product) => {
      if (product?.productId == id && skuId == product?.skuId) {
        let updatedData = { ...product }; // Create a shallow copy

        if (
          addonlist?.["product-price-group"] &&
          addonlist?.["product-price-group"]?.status == 1 &&
          token
        ) {
          const priceGroup = productPricingGroupList?.filter(
            (product) => product?.skuId == product?.skuId
          );
          const highQuantity = priceGroup?.at(-1)?.maxQuantity;
          if (priceGroup?.length > 0) {
            const tirePrice = priceGroup?.filter(
              (price) => price?.maxQuantity <= product.productCount - 1
            );
            if (tirePrice?.length > 0) {
              updatedData.price = tirePrice.at(-1)?.price;
              updatedData.priceGroup = 1;
            } else {
              updatedData.price = updatedData?.productPrice;
              updatedData.priceGroup = 0;
            }
          }
        }
        if (updatedData?.hasStock == 1) {
          if (
            updatedData?.minQuantityAllowedCart &&
            updatedData.productCount > min
          ) {
            updatedData.productCount = updatedData.productCount - 1;
            if (token && cartPath == "cartItems") {
              CartIncreaseAndDecreaseApi(updatedData);
            }
          } else {
            ToastMessage({
              type: "error",
              message: t("toastMessage.minimumLimitCart"),
            });
          }
        } else if (updatedData?.hasStock == 0) {
          if (updatedData.productCount > 1) {
            updatedData.productCount = updatedData.productCount - 1;
            if (token && cartPath == "cartItems") {
              CartIncreaseAndDecreaseApi(updatedData);
            }
          }
        }

        return updatedData; // Return updated copy
      }
      return product; // Return unchanged product if no match
    });

    localStorage.setItem(cartPath, JSON.stringify(cartdatas));
    setTrigger((pre) => pre + 1);

    if (cartPath === "cartItems") {
      dispatch(viewCartCount(cartdatas));
    }
    dispatch(checkoutCouponData(""));
    dispatch(couponDiscountValue(0));
    dispatch(couponCode(""));
    dispatch(enteredCouponCode(""));
    dispatch(couponApplied(false));
  };

  return (
    <div className="flex items-center space-x-1.5 mt-auto">
      <button
        disabled={product?.productCount == 1 ? true : false}
        onClick={handleDecrease}
        className={`bg-[#E9E9E9] rounded-full w-8 h-8 flex justify-center items-center  tooltip`}
      >
        <img src="/img/decrease.svg" alt="minus" />
      </button>
      <input
        type="number"
        value={product?.productCount}
        readOnly
        onWheel={(e) => e.target.blur()}
        className="p-3 border border-[#D1D2D4] focus:border-black rounded focus:outline-0 focus:ring-0 w-[80px] h-[30px] font-normal text-[#000000] text-[14px] text-center pointer-events-none"
      ></input>
      <button
        onClick={handleIncrease}
        className="flex justify-center items-center bg-[#E9E9E9] rounded-full w-8 h-8 tooltip"
      >
        <img src="/img/increase.svg" alt="plus" />
      </button>
    </div>
  );
};

export default CheckoutProductQuantityControl;
