// Next
import Link from "next/link";

// Components
import CheckoutProductImage from "./CartProductImage";
import CheckoutProductDescription from "./CartProductDescription";
import CheckoutProductSeller from "./CartProductSeller";
import CheckoutProductQuantityControl from "./CartProductQuantityControl";
import CheckoutProductPrice from "./CartProductPrice";
import CheckoutRemoveProduct from "./CartRemoveProduct";
import { useTranslation } from "react-i18next";

const CheckoutProduct = ({
  product,
  token,
  addonlist,
  cartPath,
  cartProducts,
  productPricingGroupList,
  setTrigger,
}) => {
  const { t } = useTranslation();
  return (
    <div className="w-full border-t border-[#EFEFEF] p-[40px_20px]">
      <div className="flex flex-col space-y-4 sm:flex-row sm:space-x-4 sm:space-y-0">
        {/* Product Image */}
        <CheckoutProductImage product={product} />

        <div className="w-full">
          <div className="flex h-full flex-col">
            {/* Product title */}
            <Link prefetch={false} href={`/products/${product?.productSlug}`}>
              <h3 className="line-clamp-2 text-base font-semibold capitalize leading-[19px] text-[#202124]">
                {product?.productNameTrans
                  ? product?.productNameTrans
                  : product?.name}
              </h3>
            </Link>

            {/* Product Description */}
            <CheckoutProductDescription product={product} />

            {/* Digital product */}
            {product?.productType == "virtual" && (
              <div className="mt-[7px] flex items-center justify-start gap-2">
                <p className="flex items-center justify-center rounded-md bg-[#eaeaea] p-1 text-[12px] text-sm text-[#000000]">
                  {t("checkout.digitalProduct")}
                </p>
                <p className="flex items-center justify-center rounded-md bg-[#eaeaea] p-1 text-[12px] text-sm text-[#000000]">
                  {t("checkout.noShippingNeeded")}
                </p>
              </div>
            )}

            <CheckoutProductSeller product={product} />

            <CheckoutProductQuantityControl
              product={product}
              token={token}
              addonlist={addonlist}
              cartPath={cartPath}
              productPricingGroupList={productPricingGroupList}
              setTrigger={setTrigger}
            />
          </div>
        </div>

        <div className="flex grow flex-col items-end justify-between">
          <CheckoutProductPrice product={product} />

          <CheckoutRemoveProduct
            product={product}
            token={token}
            cartPath={cartPath}
            cartProducts={cartProducts}
            setTrigger={setTrigger}
          />
        </div>
      </div>
    </div>
  );
};

export default CheckoutProduct;
