// Next
import Link from "next/link";

// Utils
import { imageUrl } from "@/utilities/url";

const CheckoutProductImage = ({ product }) => {
  const defaultImage = product?.productImage?.find(
    (img) => img.defaultImage == 1
  );

  return (
    <Link prefetch={false} href={`/products/${product?.productSlug}`}>
      <div className="rounded-md min-w-[200px] max-w-[200px] overflow-hidden">
        <img
          src={
            imageUrl +
            "?path=" +
            defaultImage?.containerName +
            "&name=" +
            defaultImage?.image +
            "&width=182&height=182"
          }
          alt={product?.name}
          className="w-auto max-w-full mx-auto"
          onError={({ currentTarget }) => {
            currentTarget.onerror = null;
            currentTarget.src = "/img/no-image.png";
            currentTarget.alt = "no-image";
          }}
        />
      </div>
    </Link>
  );
};

export default CheckoutProductImage;
