// Utils
import { PriceCalculate, PriceTaxCalculate } from "@/utilities/PriceCalculate";

// Components
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

const CheckoutProductPrice = ({ product }) => {
 const { t } = useTranslation();

  return (
    <div>
      {product?.priceGroup == 0 ? (
        <p className="mb-2 font-bold text-[#202124] text-xl text-end leading-6">
          <CustomCurrencyFormatter
            price={
              PriceCalculate(
                product?.price,
                product?.pricerefer,
                product?.flag,
                product?.taxType,
                product?.taxValue
              ) * product?.productCount
            }
          />
        </p>
      ) : (
        <p className="mb-2 font-bold text-[#202124] text-xl text-end leading-6">
          <CustomCurrencyFormatter
            price={
              PriceTaxCalculate(
                product?.price,
                product?.taxType,
                product?.taxValue
              ) * product?.productCount
            }
          />
        </p>
      )}

      {product?.flag == 1 && product?.priceGroup == 0 && (
        <div className="flex flex-nowrap items-center space-x-2 rtl:space-x-[unset]">
          <p className="text-sm font-normal rtl:ml-2 text-black-300 whitespace-nowrap">
            {t("productdetails.Mrp")}{" "}
            <del>
              <CustomCurrencyFormatter
                price={PriceTaxCalculate(
                  product?.price,
                  product?.taxType,
                  product?.taxValue
                )}
              />
            </del>
          </p>
          <span className="text-sm font-normal text-activegreen whitespace-nowrap">
            {Math.round(
              ((PriceTaxCalculate(
                product?.price,
                product?.taxType,
                product?.taxValue
              ) -
                PriceTaxCalculate(
                  product?.pricerefer,
                  product?.taxType,
                  product?.taxValue
                )) /
                PriceTaxCalculate(
                  product?.price,
                  product?.taxType,
                  product?.taxValue
                )) *
                100
            )}
            {t("productdetails.OFF")}
          </span>
        </div>
      )}
    </div>
  );
};

export default CheckoutProductPrice;
